@import url(https://fonts.googleapis.com/css?family=Varela+Round);
@import url(https://use.fontawesome.com/releases/v5.4.1/css/all.css);
/* core settings */
* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent; }

body {
  background-color: #f3f3f3;
  color: #555;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Varela Round', sans-serif;
  font-size: 100%;
  font-size: 1.2em;
  line-height: 1.5;
  border: 0;
  outline: 0;
  /* Optional */
  text-rendering: geometricPrecision;
  -webkit-font-smoothing: antialiased; }

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0; }

a {
  color: inheirt;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer; }

li {
  list-style: none; }

img {
  display: block;
  width: 100%;
  height: auto;
  margin: 0 auto; }

/* core (optional) */
*:focus {
  outline: none; }

button {
  border: none; }

/* Button Style */
.btn {
  display: block;
  width: 60%;
  margin: 1em auto;
  padding: .5em;
  text-align: center;
  border-radius: 100px;
  transition: .2s;
  cursor: pointer; }

/* Input Style */
input[type=text] {
  display: block;
  width: 100%;
  padding: .5em 1em;
  border: none;
  border-radius: 4px;
  margin: 10px 0;
  padding: 10px; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset; }

header {
  background-color: #f34235;
  color: #fcc9c5;
  margin-bottom: 2rem;
  padding: 1rem 0;
  text-align: center; }

.header__grid {
  display: flex;
  align-items: center;
  justify-content: center; }

.header__img-wrap {
  width: 5%; }
  .header__img-wrap img {
    opacity: .85; }

/* */
.container {
  max-width: 88%;
  margin: 0 auto; }

.input__content {
  display: flex;
  width: 100%;
  margin: 2rem auto; }
  .input__content input {
    font-size: 1.2rem;
    padding: .6rem 2rem .6rem 1rem;
    border-radius: 6rem;
    border: 1.5px solid rgba(119, 119, 119, 0.3);
    transition: border .25s; }
    .input__content input:focus {
      border: 1.5px solid #777777;
      transition: .25s; }
  .input__content .ico-mglass {
    position: relative;
    top: 1.2rem;
    right: 2.2rem; }
    .input__content .ico-mglass:before {
      content: "";
      display: inline-block;
      height: .7rem;
      width: .7rem;
      border-radius: 6rem;
      border: 2px solid #777; }
    .input__content .ico-mglass:after {
      content: "";
      position: absolute;
      top: 1.3rem;
      left: .7rem;
      background: #777;
      height: .125rem;
      width: .5rem;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }

/* Recipe Lists */
.box {
  position: relative;
  min-height: 300px;
  background-color: #fff;
  cursor: pointer;
  transition: all 0.25s ease-out;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.15); }
  .box:hover {
    box-shadow: 0px 1px 20px 0px rgba(0, 0, 0, 0.2); }
    .box:hover img {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      opacity: 0.6; }

.box__thumbnail {
  overflow: hidden;
  height: 200px;
  width: auto;
  opacity: 0.9; }
  .box__thumbnail img {
    transition: all 0.25s ease-out; }

.box__info {
  background: #fff;
  height: 8rem;
  padding: 1rem;
  z-index: 1; }
  .box__info .box__title {
    font-size: 1.2rem;
    color: #f34235;
    text-transform: uppercase;
    padding-bottom: .5rem; }
  .box__info .box__subtitle {
    font-size: 1rem; }
    .box__info .box__subtitle a {
      color: inherit; }

.btn {
  background-color: rgba(8, 67, 6, 0.7);
  color: #fff;
  font-size: 1.2rem; }
  .btn:hover {
    background-color: rgba(5, 44, 4, 0.8);
    color: #fff; }
  .btn a {
    color: #fff;
    font-weight: 200; }

/* Current Recipe */
.current-recipe .return {
  color: #1e90ff;
  padding: .5rem .7rem;
  border: 1.5px solid #1e90ff;
  border-radius: .3rem; }

.current-recipe .current-recipe__title {
  font-size: 3rem;
  color: #f34235;
  padding: 1rem 0;
  text-align: center; }

.current-recipe .current-recipe__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }

.current-recipe .current-recipe__info {
  padding: 1rem; }

.current-recipe .current-recipe__author {
  font-size: 2.5rem; }

.current-recipe .current-recipe__website {
  font-size: 1.5rem; }
  .current-recipe .current-recipe__website a {
    color: #1e90ff; }

@media screen and (min-width: 62.5rem) {
  .grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: .7rem; }
  .input__content {
    width: 40%; } }

footer {
  background-color: #f34235;
  color: #fcc9c5;
  margin-top: 2rem;
  padding: 2rem 0;
  text-align: center; }
  footer p {
    font-size: .8rem; }

